body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,body,#root,.App{
  width:100%;
  height:100%;
  overflow: hidden;

}
.App{

}
#HomeScreen{
    width:100%;
    height: calc(100% - 37px);
    background-size: cover;
    background-position: center;
    position: relative;
}
#selectBox{
    border:1px solid #8db749;
    z-index:3;
    background-color:rgba(163, 191, 135, 0.27);
}
.hideBox{
    -webkit-animation: boxFadeOut 0.55s forwards ease;
            animation: boxFadeOut 0.55s forwards ease;
}
.shortcut-wrapper{
    height:90%;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    margin: 0 0 0 75px;
    display: inline-block;
    z-index: 5;
}
.shortcut-wrapper .medium-icon{
    margin:25px 0 25px 0;
}
@-webkit-keyframes boxFadeOut{
    0%{
        opacity: 1;
    }
    99%{
        opacity: 0;
        width:unset;
        height: unset;
    }
    100%{
        border:none;
        background-color:transparent;
        width:0!important;
        height:0!important;
    }
}
@keyframes boxFadeOut{
    0%{
        opacity: 1;
    }
    99%{
        opacity: 0;
        width:unset;
        height: unset;
    }
    100%{
        border:none;
        background-color:transparent;
        width:0!important;
        height:0!important;
    }
}
.medium-icon{
    text-align: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    z-index: 4;
    width: 110px;
}
.medium-icon img{
    width:48px;
    height:48px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.medium-icon h3{
    color:white;
    margin:0;
    padding:0;
    font-size:13px;
    font-weight:400;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    padding:2px 4px;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    margin:auto;
}
.medium-icon:hover img{
    -webkit-filter:brightness(125%);
            filter:brightness(125%);
}
.medium-icon:hover h3{
    background-color:black;
}
.selected-icon .medium-icon-mask{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    margin:auto;
    position: relative;
}
.selected-icon .medium-icon-mask .layer-mask{
    width: 47px;
    height: 42.5px;
    background-color:#83b18361;
    position: absolute;
    top:2px;
    left:1px;
}

.selected-icon h3{
    color:white!important;
    background-color:#83b183!important;
    border-radius: 2px;
}
#Footer{
    width: 100%;
    height: 2.2em;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: #343434;
    border-radius: 0px;
    border-top: 2px solid #1f1d1d;
    box-shadow: inset #aba2a2 -3px 1px 22px -10px;
}
.nav{
    margin:0;
    padding:0;
    height:100%;
    display: flex;
    float:left;
}
.nav-item{
    height:100%; 
    width: -webkit-fit-content; 
    width: -moz-fit-content; 
    width: fit-content;
    display: inline-flex;
    cursor: pointer;
    margin-right:6px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.nav-item:nth-child(3){
    margin-left:2px;
}
.nav-item-icon{
    width:23px;
    height:25px;
    padding:3px 3px;
    display: inline-flex;
}
.nav-item-app-icon{
    width: 25px;
    display: inline-flex;
    height: 25px;
    margin-top: 3px;
}
.nav-item-app-icon:hover,.nav-item-icon:hover{
    box-shadow: inset snow 0px -6px 22px -12px;
    border-bottom:1px solid #fffafa85;
}
.nav-item-text{
    font-size:12px;
    color:lightgrey;
    margin:0;
    padding:0;
    top:9.2px;
    position: relative;
    display: inline-flex;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.footer-windows{
    float: left;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 100%;
    margin: 0 0 0 25px;
    padding: 0;
    list-style: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    max-width: 83%;
    overflow: hidden;
}
.selected-footer-window{
    box-shadow:inset 0px 39px 50px -16px #bdbdbd99!important;
}
.footer-windows li{
    display: inline-block;
    margin: 0 1px;
    background-color: #353535;
    box-shadow: inset 0px 0px 65px -2px #ffffff45;
    border-radius: 3px;
    width: 160px;
    height:100%;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}
.footer-windows li h3, .footer-windows li img{
    display:inline-block;
    vertical-align: middle;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.footer-windows li h3{
    color:snow;
    font-weight:normal;
    margin:5px 0 0 0;
    padding:0;
    font-size:13px;
    max-width: 117px;
    overflow-x: hidden;
    overflow-y: hidden;
    white-space: nowrap;
}
.footer-windows li img{
    width: 25px;
    height: 25px;
    margin: 5px 5px 0 8px;
}
.selected-explorer{
    z-index: 1000!important;
}
.selected-explorer #selectedBox{
    z-index: 1001;
}
.explorer-minimize{
    display:none!important;
}
.explorer-fullscreen{
    width:100%!important;
    height:calc(100% - 37px)!important;
    top:0!important;
    left:0!important;
    min-height: unset!important;
    max-width: unset!important;
}
.explorer{
    width:47%;
    height:47%;
    background-color:white;
    box-shadow: #00000080 0px 2px 20px 2px;
    position: absolute;
    top: 23%;
    left: 23vw;
    border-radius: 4px;
    z-index: 15;
    min-width:375px;
    min-height:450px;
    max-width:875px;
}
.explorer-head{
    height: 90px;
    position: relative;
    background-color:#e0dfdf;
    border-radius: 4px 4px 0 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.explorer-controls{
    margin:0;
    padding:0;
    list-style: none;
    position: absolute;
    right:0;
    top:0;
}
.explorer-controls li{
    color:#b9b9b9;
    display:inline-block;
    font-weight:bolder;
    font-size:16px;
    text-align:center;
    padding:5px 5px;
    margin:0 5px;
    cursor:pointer;
}
.explorer-title{
    margin:0;
    padding:0;
    text-align:center;
    padding-top:10px;
    font-weight: 500;
    color:grey;
    font-size:14px;
}
.explorer-settings{
    margin:0;
    padding:0;
    list-style: none;
}
.explorer-settings li{
    display:inline-block;
}
.explorer-settings-label{
    margin:0;
    padding:0;
    font-weight:lighter;
    color:#4a4848;
    font-size:13px;
    letter-spacing:0.3px;
    padding:7px;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
}
.explorer-settings-label-selected{
    color:white;
    background-color:#75a030bf;
}
.explorer-body{
    width:100%;
    height: calc(100% - 90px);
    position: relative;
}
.explorer-sidebar{
    float:left;
    width:175px;
    height: 100%;
    background-color:#403f3f;
    margin:0;
    padding:0;
    list-style: none;
}
.explorer-sidebar-section{
}
.explorer-sidebar-section-head:hover{
    background-color:#a9a3a32e;
}
.explorer-sidebar-section-head{
    padding:5px 0;
    margin:0;
    font-size:13px;
    font-weight:500;
    color:lightgrey;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.explorer-sidebar-section-icon{
    margin:0;
    padding:0 3px;
    font-size:13px;
    font-weight:500;
    color:grey;
    display:inline-block;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.explorer-sidebar-section-icon:hover{
    color:lightgrey;
}
.explorer-sidebar-items{
    display:none;
    margin:0;
    padding:0;
    list-style: none;
}
.explorer-sidebar-items li{
    padding:5px 0 5px 35px;
    font-size: 13px;
    color: lightgrey;
    font-weight: lighter;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.explorer-sidebar-items li:hover{
    background-color:#a9a3a32e;
}
.explorer-content{
    margin:0;
    padding:0;
    list-style:none;
    text-align:left;
    width:calc(100% - 175px);
    height:100%;
    float:right;
    overflow-y: scroll;
    overflow-x: hidden;
    position: relative;
}
.explorer-content li{
    vertical-align: top;
    display:inline-block;
    padding:10px;
}
.explorer-content li h3{
    color:#585858;
}
.explorer-content .medium-icon:hover h3{
    background-color:transparent;
}
.explorer-nav{
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
}
.explorer-nav div{
    display:inline-block;
    width:19px;
    height:19px;
    padding:5px 7px;
    margin:0 0 0 10px;
    border-radius: 2px;
}
.explorer-nav div:hover{
    background-color:#f5f5f5c4;
    box-shadow: 0px 0px 2px 0px #4e44448c;
}
.explorer-nav .explorer-nav-disabled img{
    -webkit-filter:none!important;
            filter:none!important;

}
.explorer-nav .explorer-nav-disabled:hover{
    background-color:unset;
    box-shadow: unset;
}
.explorer-nav div img{
    width:100%;
    height:100%;
    -webkit-filter:contrast(0%);
            filter:contrast(0%);
}
.settings-dropdown{
    margin:0;
    padding: 5px 0px;
    list-style: none;
    background-color:white;
    box-shadow: black 0px 0 15px -7px;
    border:1px solid #00000021;
    border-radius:1px;
    position: absolute;
    z-index: 5;
}
.settings-dropdown li{
    display:block;
    font-size:11px;
    padding:5px 0;
}
.settings-dropdown li:hover{
    background-color:#75a030bf;
}
.settings-dropdown li:hover .settings-dropdown-title,.settings-dropdown li:hover .settings-dropdown-shortcut{
    color:white;
}
.settings-dropdown-shortcut,.settings-dropdown-title{
    margin:0;
    padding:0;
    font-weight:400;
    display:inline-block;  
}
.settings-dropdown-title{
    padding:0 15px 0 25px;
    color:#333333;
}
.settings-dropdown-shortcut{
    float:right;
    padding:0 7px 0 40px;
    font-weight:lighter;
    color:grey;
}
.explorer-fullscreen{
    width:100%!important;
    height:calc(100% - 37px)!important;
    top:0!important;
    left:0!important;
    min-height: unset!important;
    max-width: unset!important;
}
.explorer-minimize{
    display:none!important;
}
.file-viewer-head{
    height:36px!important;
}
.file-viewer-body{
    position: relative;
    width:100%;
    height:calc(100% - 36px);
}
.file-viewer-content{
    overflow-y: scroll;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    margin: 0;
    pointer-events: none;
}
.selected-explorer .pdf-viewer-content{
    pointer-events: unset;
}
.pdf-viewer-content{
    overflow-y: scroll;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    margin: 0;
    pointer-events: none;
}
.text-viewer-content{
    overflow-y: scroll;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 10px 16px 1px 16px;
    margin: 0;
    word-wrap: break-word;
}
.image-viewer-content{
    overflow-y: scroll;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    margin: 0;
}
.image-viewer-content img{
    width: 100%;
    height: 100%;
}
.popup-modal{
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    min-width: 220px;
    min-height: 100px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.popup-modal-head{
    height:36px !important;
}
.popup-modal-body{
    padding:15px 0px;
}
.popup-modal-question{
    font-weight: normal;
    font-size:15px;
    color:grey;
    text-align: center;
    margin:3px 0 20px 0;
    padding:0;
}
.popup-modal-options{
    margin:0;
    padding:0;
    list-style: none;
    text-align: center;
}
.popup-modal-options li{
    font-weight:normal;
    color:grey;
    display: inline-block;
    font-size:13px;
    background-color:#f5f5f5c4;
    border:1px solid #8e888840;
    padding:10px 20px;
    cursor:pointer;
}
.popup-modal-options li:first-child{
    border-radius: 2px 0 0 2px;
}
.popup-modal-options li:last-child{
    border-radius: 0px 2px 2px 0px;
}
.popup-modal-options li:nth-child(even){
    border-left:none;
}
.popup-modal-options li:hover{
    background-color:white;
}
